import React from "react";

import GithubColor64 from "../../assets/link_icons/github-color-64.png";
import LinkedInColor64 from "../../assets/link_icons/linkedin-color-64.png";
import MediumColor64 from "../../assets/link_icons/medium-color-64.png";

import GithubBW64 from "../../assets/link_icons/github-bw-64.png";
import LinkedInBW64 from "../../assets/link_icons/linkedin-bw-64.png";
import MediumBW64 from "../../assets/link_icons/medium-bw-64.png";

import GithubColor32 from "../../assets/link_icons/github-color-32.png";
import LinkedInColor32 from "../../assets/link_icons/linkedin-color-32.png";
import MediumColor32 from "../../assets/link_icons/medium-color-32.png";

class Links extends React.Component {
  constructor() {
    super();
    this.state = {
      linkedIn: false,
      medium: false,
      github: false,
      resume: false
    };
  }

  isHovered = image => {
    this.setState({
      [image]: true
    });
  };

  stopHovering = image => {
    this.setState({
      [image]: false
    });
  };

  returnLinkedInImage() {
    if (this.props.size === "64") {
      if (this.state.linkedIn) {
        return <img src={LinkedInColor64} alt="" />;
      } else {
        return <img src={LinkedInBW64} alt="" />;
      }
    } else if (this.props.size === "32") {
      return <img src={LinkedInColor32} alt="" />;
    }
  }

  returnGithubImage() {
    if (this.props.size === "64") {
      if (this.state.github) {
        return <img src={GithubColor64} alt="" />;
      } else {
        return <img src={GithubBW64} alt="" />;
      }
    } else {
      return <img src={GithubColor32} alt="" />;
    }
  }

  returnMediumImage() {
    if (this.props.size === "64") {
      if (this.state.medium) {
        return <img src={MediumColor64} alt="" />;
      } else {
        return <img src={MediumBW64} alt="" />;
      }
    } else {
      return <img src={MediumColor32} alt="" />;
    }
  }

  render() {
    const LinkedIn = this.returnLinkedInImage();
    const Github = this.returnGithubImage();
    const Medium = this.returnMediumImage();
    return (
      <div className="row p-0 m-0">
        <div className="col" />
        <div
          onMouseEnter={() => this.isHovered("github")}
          onMouseLeave={() => this.stopHovering("github")}
          className="mr-5"
        >
          <a target="-blank" href="https://github.com/JonathanYeeW">
            {Github}
          </a>
        </div>
        <div
          onMouseEnter={() => this.isHovered("linkedIn")}
          onMouseLeave={() => this.stopHovering("linkedIn")}
          className="mr-5"
        >
          <a target="-blank" href="https://www.linkedin.com/in/jonathanwyee">
            {LinkedIn}
          </a>
        </div>
        <div
          onMouseEnter={() => this.isHovered("medium")}
          onMouseLeave={() => this.stopHovering("medium")}
        >
          <a target="-blank" href="https://medium.com/@Jnothn">
            {Medium}
          </a>
        </div>
        <div className="col" />
      </div>
    );
  }
}

export default Links;
