import React from "react";
import Separator from "../utils/Separator";
import WorkProfiles from "../../database/Work";

class Experience extends React.Component {
  constructor() {
    super();
    this.state = {
      work_profile: WorkProfiles
    };
  }

  render() {
    return (
      <div id="experience" className="tertiary_text_color">
        <Separator title="{ experience };" />
        <div className="contianer">
          <div className="row pt-5">
            <div className="col-xl-2 col-lg-1 col-md-1 col-sm-0" />
            <div className="col">
              {this.state.work_profile.map((prof, index) => {
                return (
                  <div key={index} className="pb-5">
                    <Profile profile={prof} />
                  </div>
                );
              })}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

function Profile(props) {
  return (
    <div>
      <div className="row">
        <div className="profile_image_container">
          <a href={props.profile.website}>
            <img className="profile_image" src={props.profile.image} alt="" />
          </a>
        </div>
        <div className="profile_text_container border rounded">
          <h4>{props.profile.title}</h4>
          <h6>{props.profile.date}</h6>
          <hr />
          <p>{props.profile.description}</p>
          <hr />
          <p>{props.profile.tech.join(", ")}</p>
        </div>
      </div>
    </div>
  );
}

export default Experience;
