import React from "react";
import Links from "./utils/Links";

class Footer extends React.Component {
  constructor() {
    super();
    this.state = {
      name: "",
      email: "",
      message: "",
      formSubmitted: false,
      year: new Date().getFullYear(),
      version: "1.0.4"
    };
  }

  onChangeHandler = event => {
    event.preventDefault();
    this.setState({
      [event.target.name]: event.target.value
    });
  };

  submitForm = () => {
    this.setState(prevState => {
      return {
        formSubmitted: !prevState.formSubmitted
      };
    });
  };

  formSpreeFormReturn = formSubmitted => {
    if (formSubmitted) {
      return <p>your message has been sent!</p>;
    } else {
      return (
        <form
          method="POST"
          action="https://formspree.io/Jonathan.Yee.W@gmail.com"
        >
          <div className="form-group">
            <input
              type="email"
              name="email"
              placeholder="your email"
              className="form-control"
            />
          </div>
          <div className="form-group">
            <textarea
              name="message"
              rows="5"
              placeholder="your message"
              className="form-control"
            />
          </div>
          <button className="btn btn-outline-light" type="submit">
            Submit
          </button>
        </form>
      );
    }
  };

  render() {
    const formView = this.formSpreeFormReturn(this.state.formSubmitted);
    return (
      <div id="Footer" className="p-o m-0">
        <h1>Contact</h1>
        <p className="secondary_text_color">
          Have a question or want to work together?
        </p>
        <div className="row p-0 m-0">
          <div className="col" />
          <div className="col-xl-5 col-lg-5 col-md-6 col-sm-8 col-8 contact_container">
            {formView}
          </div>
          <div className="col" />
        </div>
        <div id="subfooter" className="">
          <div className="mt-2 mb-3">
            <Links size="32" />
          </div>
          <p className="p-0 m-0">
            Jonathan Yee | Copyright {this.state.year} | {this.state.version}
          </p>
          <p className="p-0 m-0 subfooter_references">
            Icons made by{" "}
            <a
              href="https://www.flaticon.com/authors/freepik"
              title="Freepik"
              className="subfooter_references"
            >
              Freepik
            </a>{" "}
            from{" "}
            <a
              href="https://www.flaticon.com/"
              title="Flaticon"
              className="subfooter_references"
            >
              www.flaticon.com
            </a>{" "}
            messages.attribution.is_licensed_by{" "}
            <a
              href="http://creativecommons.org/licenses/by/3.0/"
              title="Creative Commons BY 3.0"
              target="_blank"
              className="subfooter_references"
            >
              CC 3.0 BY
            </a>
          </p>
        </div>
      </div>
    );
  }
}

export default Footer;
